@import "../node_modules/normalize-scss/sass/normalize";
@include normalize(); // call in normalize via mixin

@import "variables";
@import "responsive-nav";
@import "page";
@import "running";

@media screen and (min-width: 400px) {
    picture,
    img {
        max-width: none;
        width: auto;
        display: block;
        margin: auto;
    }
}

@media screen and (min-width: 700px) {
    .post-content {
        width: 700px;
    }
}

@media screen and (min-width: 1020px) {
    .header,
    main,
    footer,
    .container {
        width: 1000px;
        margin: 1em auto;
    }

    .page--home .container--home {
        display: flex;
    }

    .page--home section {
        position: relative;
        padding-bottom: 4rem;
    }

    .btn--right {
        position: absolute;
        bottom: 0;
        left: 0;
    }

    .full-width {
        width: calc(100% - 100px);
        margin: auto;

    }
}
